import React from 'react';

import Api from 'api';
import classNames from 'classnames';
import styles from "./process-tracking.scss";
import * as Tool from 'tool';
import * as Common from 'tool/common';
import * as getUserIdInfo from 'tool/getUserId';

import { inject, observer } from 'mobx-react';

import Gallery from 'components/base/gallery';
import LazyLoad from 'react-lazy-load';
import Loading from 'components/base/loading-inner';
@inject('Toast', 'ComNoContent', 'Header')
@observer
export default class ProcessTracking extends React.Component {
  constructor (props){
    super(props);
    this.state = {
      pageRender: false,
      liveList: [], // 进程实况
      progressList: [], // 进程列表
      orderInfo: {}, // 订单相关信息
      currentTabType: 1, // 1:服务进程 2: 实况
      currentImgList: [], // 大图预览数组
      isGallertShow: false,
      isLoading: false
    };
  }

  componentDidMount() {
    this.props.ComNoContent.$comConntent('');
    this.props.Header.$setTitle('服务进程跟踪');
    Tool.setTitle('服务进程跟踪');
    this.setState({
      isLoading: true
    });
    let { userId } = getUserIdInfo.getUserId();
    Common.trackCustomPageInit({
      'pageId': 'tmfy_order_progress_pv',
      'site_id': 1211120,
      'category_id': 1595,
      'sub_category_id': 18476
    }, {
      'userId': userId
    });

    // 获取服务进程追踪
    let params = {
      orderId: Tool.getUrlParam('orderId') || ''
    };
    Api.getProcessTracking(params).then((res) => {
      this.setState({
        isLoading: false
      });
      if (res.returncode === 0) {
        let orderInfo = Object.assign({}, this.state.orderInfo, {orderId: res.result.orderId, orderState: res.result.orderState, orderStateDesc: res.result.orderStateDesc});
        this.setState({
          liveList: [...res.result.liveList],
          progressList: [...res.result.progressList],
          orderInfo: orderInfo,
          pageRender: true
        });
      } else if (res.returncode === 501) { // 未登录
        // let that = this;
        Common.handleLogin({
          backUrl: encodeURIComponent(window.location.href),
          queryParams: {},
          successCb: () => {
            window.location.reload();
          },
          failCb: () => {
          }
        });
        this.setState({
          reservationsList: [],
          pageRender: false
        });
        this.props.ComNoContent.$comConntent(res.message);
      } else {
        this.props.ComNoContent.$comConntent(res.message);
      }
    }).catch((error) => {
      this.setState({
        isLoading: false
      });
      this.props.ComNoContent.$comConntent(error.message || '页面溜走了，请稍候重试');
    });
  }
  // methods

  // 点击统计事件
  handleTrackCustomEvent(action, objectId) {
    let { userId } = getUserIdInfo.getUserId();
    // eslint-disable-next-line
    trackCustomEvent('yc_page_common_click', {
      'bus': '1',
      'refpage': Tool.getCookie('refpage'),
      'channel': Tool.getCookie('channel'),
      'city': Tool.getCookie('cityId') || 110100,
      'action': action
    }, {
      'userId': userId,
      'objectId': objectId
    });
  }

  // tab切换
  tabChangeHandel(type) {
    this.handleTrackCustomEvent('tmfy_progress_tab_click', type);
    this.setState({
      currentTabType: type
    });
  }

  // 大图预览
  showGallery(item, type, index, k) {
    // type: 1,2    2:车辆保养二级图片预览
    let originImages = [];
    if (type === 1) {
      originImages = item.imgList;
    } else if (type === 2) {
      originImages = item.maintainItems[index].itemImgList;
    }
    if (Tool.webApp.checkMainApp()) { // 主软&点评app
      let autoAppOriginImages = [];
      for (let i = 0, len = originImages.length; i < len; i++) {
        let tmpObj = {
          'url': originImages[i],
          'title': ''
        };
        autoAppOriginImages.push(tmpObj);
      }
      let idx = type === 1 ? index : k;
      // eslint-disable-next-line
      require('jsBridge');
      window.jsBridgeV2.bigimg({
        'currentindex': idx + 1,
        'image': autoAppOriginImages,
        success(result) {
          console.log(JSON.stringify(result));
        },
        fail(result) {
          console.log(JSON.stringify(result));
        }
      });
    } else { // M端&违章
      this.setState({
        isGallertShow: true,
        currentImgList: originImages
      }, () => {
        let idx = type === 1 ? index : k;
        this.refs.Gallery.renderSwiper();
        this.refs.Gallery.slideToIndex(idx);
      });
    }
  }

  // 关闭H5大图预览组件
  hideGallery() {
    this.setState({
      isGallertShow: false
    });
  }

  render() {
    const {
      pageRender,
      currentTabType,
      isGallertShow,
      liveList,
      progressList,
      orderInfo,
      currentImgList,
      isLoading
      } = this.state;
    return (
      pageRender && <div className={styles.process_tracking_container}>
        <p className={classNames(styles.process_state)}>{ orderInfo.orderStateDesc }</p>
        <div className={styles.content_wrapper}>
          <ul className={styles.tab_wrapper}>
            <li className={classNames({[styles.active]: currentTabType === 1})} onClick={() => this.tabChangeHandel(1)}>服务进程</li>
            <li className={classNames({[styles.active]: currentTabType === 2})} onClick={() => this.tabChangeHandel(2)}>透明车间实况</li>
          </ul>
          {/* 服务进程tab */}
          {
            (currentTabType === 1) && (<ul className={styles.process_tab_wrapper}>
              {
                progressList.map((item, index) => {
                  return (
                    <li key={item.progressCode || index}>
                      <p className={classNames(styles.point, {[styles.active]: item.inProgress})}></p>
                      <div className={styles.right_content}>
                        <p className={classNames(styles.process_name, {[styles.active]: item.inProgress})}>{ item.progressDesc }</p>
                        <p className={styles.process_time}>{ item.startTime }</p>
                      </div>
                    </li>
                  );
                })
              }
            </ul>)
          }
          {/* 实况tab */}
          {
            (currentTabType === 2) && (<div className={styles.live_tab_wrapper}>
              {/* <div className={styles.vehicle_workspace}>
                <p>车辆当前所在位置</p>
                <p>A区</p>
              </div> */}
              <ul className={styles.process_list_container}>
                {
                  liveList.map((item, index) => {
                    return (
                      <li key={item.progressCode || index}>
                        <p className={styles.live_process_title}>{ item.progressDesc }</p>
                        <p className={styles.live_process_time}>{ item.startTime }</p>
                        <ul className={styles.process_list_wrapper}>
                          {
                            item.imgList.map((val, key) => {
                              return (
                                <li key={key}>
                                  <div className={styles.img_wrapper} onClick={() => this.showGallery(item, 1, key)}>
                                    <LazyLoad width={223} height={149}>
                                      <img src={val.replace(/https?:/, '')} alt=""/>
                                    </LazyLoad>
                                  </div>
                                </li>
                              );
                            })
                          }
                          {
                            item.maintainItems.map((itm, idx) => {
                              return (
                                <li key={idx} className={styles.baoyang_list}>
                                  <p className={styles.baoyang_title}>{ `${idx + 1}.${itm.itemName}` }</p>
                                  <div className={styles.baoyang_img_list}>
                                    {
                                      itm.itemImgList.map((url, i) => {
                                        return (
                                          <div onClick={() => this.showGallery(item, 2, idx, i)}  key={i}>
                                            <LazyLoad width={223} height={149}>
                                              <img src={url.replace(/https?:/, '')} alt=""/>
                                            </LazyLoad>
                                          </div>
                                        );
                                      })
                                    }
                                  </div>
                                </li>
                              );
                            })
                          }
                        </ul>
                      </li>
                    );
                  })
                }
              </ul>
              {
                !liveList.length && <div className={styles.process_list_empty}>
                  <div className={styles.empty_icon}></div>
                  <div className={styles.empty_text}>暂无实况可查看</div>
                </div>
              }
            </div>)
          }
        </div>

        {/* 施工师傅 */}
        {/* <div className={styles.construction_person_wrapper}>
          <p className={styles.construction_person_title}>施工师傅</p>
          <div className={styles.construction_person_content}>
            <div className={styles.construction_person_portrait}>
              <img src={require('../../assets/images/placeholder.png')} alt=""/>
              <p className={styles.level_label}>高级技师</p>
            </div>
            <div className={styles.construction_person_info}>
              <p className={styles.name}>方亮</p>
              <p className={styles.work_number}>
                <span>技师工号：</span>
                <span>EUJ12345567</span>
              </p>
              <div className={styles.tel_num_wrapper}>
                <p className={styles.service_length}>工龄：5年</p>
                <p className={styles.tel_num}>联系方式：18201668137</p>
              </div>
            </div>
          </div>
          <p className={styles.online_consulting_btn}>在线咨询</p>
        </div> */}
        <Gallery show={isGallertShow} list={currentImgList} onHideGallery={this.hideGallery.bind(this)} ref="Gallery"/>
        <Loading show={ isLoading } />
      </div>
    );
  }
}